<template>
    <v-card

            dark
            color="blue"
            width="350"
    >
        <v-card-title class="title">
            {{displayName}}
        </v-card-title>
        <v-card-subtitle style="">
            <div
            >
                {{description}}
            </div>
        </v-card-subtitle>
        <v-card-text class="white  pa-1">
            <v-container fluid>
                <v-row>
                    <v-col class="pt-0">
                        <v-text-field
                                :label="param.name"
                                color="primary"
                                light
                                v-for="(param,index) in method.parameterInfos"
                                :hint="param.typeName"
                                :type="getInputType(param.typeName)"
                                :key="index"
                                v-model="parameters[index]"
                        >

                        </v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-btn
                                color="primary"
                                class="mx-0"
                                block
                                @click="execute"
                                :loading="isExecuting"

                        >
                            执行!
                            <v-icon
                                    right
                                    dark
                            >
                                mdi-rocket-launch
                            </v-icon>
                        </v-btn>
                    </v-col>
                </v-row>

                <v-slide-x-reverse-transition
                        hide-on-leave
                >
                    <v-row>
                        <v-col class="pb-0 pt-0">
                            <v-expansion-panels v-model="resultPanelExpandState">
                                <v-expansion-panel
                                >
                                    <v-expansion-panel-header color="secondary">
                                        执行结果
                                    </v-expansion-panel-header>
                                    <v-expansion-panel-content color="secondary">
                                        <v-timeline
                                                align-top
                                                dense
                                        >
                                            <v-slide-x-reverse-transition
                                                    group
                                                    hide-on-leave
                                            >
                                                <v-timeline-item
                                                        color="white"
                                                        v-for="(returnValue,index) in returnValues"
                                                        :key="-returnValue.timestamp"
                                                        fill-dot
                                                        small
                                                >
                                                    <div>
                                                        <div class="font-weight-normal">
                                                            <strong>第{{getCurrentReturnValueIndex(index)}}次执行</strong>
                                                            @{{returnValue.displayTime}}
                                                        </div>
                                                        <div>{{returnValue.value}}</div>
                                                    </div>
                                                </v-timeline-item>
                                            </v-slide-x-reverse-transition>
                                        </v-timeline>
                                    </v-expansion-panel-content>
                                </v-expansion-panel>

                            </v-expansion-panels>
                        </v-col>
                    </v-row>
                </v-slide-x-reverse-transition>
            </v-container>


        </v-card-text>

    </v-card>
</template>
<script>
    export default {
        name: 'MethodPanel',
        props: {
            method: Object,
        },
        mounted() {
            this.method.parameterInfos.forEach(() => this.parameters.push(null))
        },
        data() {
            return {
                returnValues: [],
                parameters: [],
                resultPanelExpandState: [],
                isExecuting: false,
            }
        },
        methods: {
            async execute() {
                //TODO: add type check
                this.isExecuting = true;
                this.parameters = this.parameters.map(it => (it.typeName==='number')?(+it):(it));

                const req = await this.$http.post("call/" + this.method.hashcode, this.parameters);
                if(req.status===200)
                {
                    const date = new Date();
                    this.returnValues.unshift({
                        displayTime: this.getReadableTimeString(date),
                        timestamp: date.getTime(),
                        value: req.data
                    });
                    this.setResultPanelOpen();
                }
                else
                {
                    //TODO:Change to a more friendly error message
                    alert(req.status+":"+req.data)
                }
                await this.$nextTick();
                this.isExecuting = false;
                console.log(req.data)
            },
            getCurrentReturnValueIndex(index) {
                return this.returnValues.length - index

            },
            getReadableTimeString(date) {
                return date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds()
            },
            setResultPanelOpen() {
                this.resultPanelExpandState = 0;
            },

            /*
            TODO:Add support for more detailed numeric types,
              such as support for more detailed display of real numbers and integers, etc.
             */
            getInputType(typename) {
                switch (typename) {
                    case 'string':
                        return 'text';
                    case 'number':
                        return 'number';
                    case 'bool':
                        //Add a selector for bool values, selecting true and false
                        return 'text';
                }
            }

        },
        computed: {
            displayName: vm => vm.method.displayName ?? vm.method.name,
            description: vm => vm.method.description ?? vm.method.name
        }
    }
</script>

